<template>
  <div>
    <div class="portHeader">
      <button @click="CreateDialog"></button>
    </div>
    <div class="portContent">
      <el-table ref="multipleTable" :data="tables" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column prop="port_name" label="">
        </el-table-column>
        <el-table-column label="IP">
          <template slot-scope="scope">
            <div v-for="ip of scope.row.fixed_ips" :key="ip.ip_address">{{ ip.ip_address }}</div>
          </template>
        </el-table-column>
        <el-table-column prop="mac_address" label="MAC">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div v-if="scope.row.device_id">{{ scope.row.device_id }}</div>
            <div v-else></div>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="">
        </el-table-column>
        <el-table-column prop="admin_state_up" label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!-- <el-button size="mini" @click="EditDialog(scope.$index, scope.row)"></el-button> -->
            <el-button size="mini" type="danger" @click="DeleteDialog(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  dialog -->
    <el-dialog title="" :visible.sync="create.dialog" width="400px">
      <el-form label-width="100px">
        <el-form-item label="">
          <el-input v-model="create.name" size="mini"></el-input>
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model="create.admin_state_up"></el-checkbox>
        </el-form-item>
        <el-form-item label="ID">
          <el-input v-model="create.device_id" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="create.device_owner" size="mini"></el-input>
        </el-form-item>
        <el-form-item label=" IP ">
          <el-select v-model="create.IPorSubnet">
            <el-option value=""></el-option>
            <!-- <el-option value="subnet"></el-option>
            <el-option value="IP">IP</el-option> -->
          </el-select>
        </el-form-item>
        <!-- <el-form-item v-if="create.IPorSubnet === ''" label="">
          <el-input v-model="create.gateway_ip" size="mini"></el-input>
        </el-form-item>
        <el-form-item v-if="!create.gateway" label="IP">
          <el-input v-model="create.gateway_ip" size="mini"></el-input>
        </el-form-item> -->
        <!-- <el-form-item v-if="!create.gateway" label="MAC">
          <el-input v-model="create.gateway_ip" size="mini"></el-input>
        </el-form-item> -->
        <el-form-item>
          <el-checkbox v-model="create.port_security_enabled"></el-checkbox>
        </el-form-item>
        <el-form-item label="VNIC">
          <el-select v-model="create.IPorSubnet">
            <el-option value="normal"></el-option>
            <el-option value="macrtap"></el-option>
            <el-option value="direct"></el-option>
            <el-option value="baremetal"></el-option>
            <el-option value="direct-physical"></el-option>
            <el-option value="virtio-forwarder"></el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="create.dialog = false"> </el-button>
        <el-button type="primary" @click="createPort()"> </el-button>
      </span>
    </el-dialog>

    <!--  dialog -->
    <el-dialog title="" :visible.sync="edit.dialog" width="400px">
      <span slot="footer" class="dialog-footer">
        <el-button @click="edit.dialog = false"> </el-button>
        <el-button type="primary" @click="editPort()"> </el-button>
      </span>
    </el-dialog>

    <!--  dialog -->
    <el-dialog title="" :visible.sync="del.dialog" width="400px">
      <span slot="footer" class="dialog-footer">
        <el-button @click="del.dialog = false"> </el-button>
        <el-button type="primary" @click="deletePort()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'port',
    data() {
      return {
        tables: [],
        multipleTable: [],
        network_id: '',
        create: {
          dialog: false,
          gateway: false,
          gateway_ip: '',
          admin_state_up: true,
          port_security_enabled: true,
          binding_vnic_type: 'normal',
          device_id: '',
          device_owner: '',
          subnet_id: '',
          ip_address: '',
        },
        del: {
          dialog: false
        },
        edit: {
          dialog: false
        },

      }
    },
    methods: {
      getPort(network_id) {
        axios.post('/network/listports', {
            network_id
          })
          .then((res) => {
            this.tables = res.data.portInfo
          })
      },
      getNetwork() {
        let intervalID = setInterval(() => {
          let network_id = sessionStorage.getItem('network')
          if (network_id) {
            clearInterval(intervalID)
            this.network_id = network_id
            this.getPort(network_id)
          } else {
            console.log(network_id)
          }
        }, 100)
      },
      handleSelectionChange() {

      },
      CreateDialog() {
        this.create.dialog = true
      },
      createPort() {
        this.create.dialog = false
        let data = {
          network_id: this.network_id,
          name: this.create.name,
          admin_state_up: this.create.admin_state_up,
          port_security_enabled: this.create.port_security_enabled,
          binding_vnic_type: this.create.binding_vnic_type,
          device_id: this.create.device_id,
          device_owner: this.create.device_owner,
          subnet_id: this.create.subnet_id,
          ip_address: this.create.ip_address,
        }
        axios.post('/network/createPort', data)
      },
      EditDialog(index, row) {
        this.edit.dialog = true
      },
      editPort() {
        this.edit.dialog = false
      },
      DeleteDialog(index, row) {
        this.del.dialog = true
        this.del.port_id = row.port_id
      },
      deletePort() {
        this.del.dialog = false
        let data = {
          port_id: this.del.port_id
        }
        axios.post('/network/deletePort', data)
      },


    },
    mounted: function () {
      this.getNetwork()
    }
  }
</script>

<style>

</style>


